<template>
  <div class="newmaindre">
    <!--  -->
    <div class="dingtiiti" @click="gobackkk">
      <div class="fanhuian">
        <van-icon size="20px" name="arrow-left" />
      </div>
      <div class="yiuiui"></div>
    </div>
    <!-- <van-icon name="manager" slot="left-icon"/> -->
    <div class="kaoshangmian">
      <!-- <van-cell-group>
        <van-cell title="" icon="contact" />
        <van-cell title="13745634890" icon="orders-o" />
        <van-cell title="" icon="location-o" />
        <van-cell title="" icon="aim" />
      </van-cell-group>-->

      <van-cell-group>
        <van-field @focus="chucuhcu" v-model="username" placeholder="" left-icon="contact" />
        <van-field @focus="chucuhcu" v-model="phone" placeholder="" left-icon="orders-o" />
        <van-field @focus="showfou" v-model="city" placeholder="" left-icon="location-o" />
        <van-field @focus="chucuhcu" v-model="details" placeholder="" left-icon="aim" />
      </van-cell-group>
    </div>
    <!--  -->
    <div class="fengege"></div>
    <!--  -->
    <div class="mozhi">
      <!-- <van-radio checked-color="#FF7900"></van-radio> -->
      <van-checkbox
        v-model="checked"
        checked-color="#FF7900"
        :disabled=" username=== '' || phone ==='' ||   city === '' || details === '' ? true : false"
      ></van-checkbox>
    </div>
    <!--  -->
    <div class="baoan" @click="baocun"></div>

    <!--  -->
    <van-popup v-model="show" position="bottom" :style="{ height: '40%' }">
      <!--  -->
      <div v-show="show">
        <van-area
          :area-list="areaList"
          :columns-placeholder="['', '', '']"
          value="110101"
          @confirm="onAddrConfirm"
          @cancel="quxiao"
        ></van-area>
      </div>
    </van-popup>
  </div>
</template>

<script>
import areaList from '../../assets/js/area'
export default {
  //  
  components: {
    vanArea: areaList
  },
  data() {
    return {
      checked: false,
      // 
      areaList: null,
      // 
      show: false,
      // 
      username: '',
      // 
      phone: '',
      // 
      city: '',
      // 
      details: '',
      // id
      province: '',
      // id
      shi: '',
      // id
      district: ''
    }
  },
  created() {
    this.init()
  },
  methods: {
    init() {
      // 
      this.areaList = areaList // 
    },
    onAddrConfirm(e) {
      // 
      // ,
      console.log('this.onAddrConfirm=>e', e)
      this.province = e[0].name
      this.shi = e[1].name
      this.district = e[2].name
      this.city = e[0].name + e[1].name + e[2].name
      this.show = false
    },
    // 
    quxiao() {
      this.show = false
    },
    gobackkk() {
      this.$router.go(-1)
    },
    // checked  false
    chucuhcu() {
      this.checked = false
    },
    // 
    showfou() {
      this.show = true
      this.checked = false
    },
    //     
    async baocun() {
      // 
      let tozhi = window.sessionStorage.getItem('token')
      const { data: res } = await this.$http.post('UserAddress/add', {
        post: {
          province: this.province,
          city: this.shi,
          area: this.district,
          details_site: this.details,
          name: this.username,
          phone: this.phone,
          type: this.checked,
          token: tozhi
        }
      })
      console.log(res)
      if (res.status !== 10001) return this.$toast('')
      this.$toast('')
      this.$router.push({
        path: '/fensou'
      })
    }
  }
}
</script>

<style lang='less' scoped>
.newmaindre {
  width: 100%;
  height: 100%;
  //
  .dingtiiti {
    position: fixed;
    z-index: 120;
    background-color: #ff7900;
    width: 100%;
    height: 80px;
    top: 0;
    .fanhuian {
      position: absolute;
      top: 50%;
      color: #fff;
      left: 20px;
    }
    .yiuiui {
      position: absolute;
      font-size: 20px;
      color: #fff;
      left: 50%;
      top: 45%;
      transform: translate(-50%);
    }
  }
  .kaoshangmian {
    width: 100%;
    margin-top: 80px;
  }
  .fengege {
    width: 100%;
    height: 15px;
    // margin-top: px;
    background-color: #eeeeee;
  }
  .mozhi {
    margin-left: 15px;
    margin-top: 15px;
  }
  .baoan {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    //   top: 400rpx;
    width: 90%;
    height: 40px;
    background-color: #ff7900;
    border-radius: 20px;
    margin-top: 40px;
    color: #ffff;
    line-height: 40px;
    text-align: center;
  }
}
</style>
