      <van-cell-group :border="false">
        <van-field
          v-model="loginname"
          placeholder=""
          label=""
          left-icon="contact"
          autosize
          size="large"
        />

        <van-field
          v-model="password"
          placeholder=""
          label=""
          left-icon="orders-o"
          autosize
          size="large"
          type="password"
        />
      </van-cell-group>
    </div>
    <!--  -->
    <div class="buttontt" @click="handleClick"></div>
    <!--  -->
    <div class="forgetpas" @click="gomima"></div>
    <!--  -->
    <div class="dibu">
      <van-divider :style="{ color: '#808080', borderColor: '#808080', padding: '0 16px' }"></van-divider>
    </div>
    <!--  -->
    <div class="tubiao">
      <div class="kuangyi">
        <span class="dhe">
          <img src="../assets/images/wx.png" alt />
        </span>

        <span></span>
      </div>
      <div class="kuangyi">
        <span class="dhe">
          <img src="../assets/images/QQ.png" alt />
        </span>
        <span>QQ</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loginname: '13828776965',
      password: '123456'
    }
  },
  created() {},
  methods: {
    // 
    gozhuce() {
      this.$router.push({
        path: '/zhuce'
      })
    },
    gomima() {
      this.$router.push({
        path: '/forget'
      })
    },
    // 
    async handleClick() {
      //  
      if (this.loginname === '') {
        this.$toast('')
        return
      }
      if (this.password === '') {
        this.$toast('')
        return
      }
      // 
      let post = {
        loginname: this.loginname,
        password: this.password
      }
      // JSON.stringify(post)
      const { data: res } = await this.$http.post('Login/login', {
        post: JSON.stringify(post)
      })
      console.log(res)
      if (res.status !== 10001) return this.$toast('!')

      this.$toast('')
      // 1.  token sessionStorage 
      //   1.1 API
      //   1.2 token  token  sessionStorage 
      window.sessionStorage.setItem('token', res.token)
      // 2.  /home
      this.$router.push('/home')

      // var params = new URLSearchParams()
      // params.append("loginname", JSON.stringify(this.loginname))
      // params.append("password", JSON.stringify(this.password))
      // this.axios
      //   .post(
      //     "http://test.tuolve.com/xinliliang/web//tl_api.php?s=/Login/login",
      //     params
      //   )
      //   .then(res => {
      //     console.log(res)
      //   })
      //   .catch(err => {
      //     console.log(err)
      //   })
    }
  }
}
</script>

